async function getArticleList() {
  return await fetch(
    'https://api.github.com/repos/xiaozhaoqi/privateRepository/contents/files/article',
    {
      method: 'GET',
      headers: {
        Authorization: 'token ' + leftToken + rightToken,
      },
    }
  )
    .then((res) => {
      if (res.status < 299) return res.json()
      else return null
    })
    .then((res) => {
      return res
    })
}
async function getArticle(name) {
  return await fetch(
    'https://api.github.com/repos/xiaozhaoqi/privateRepository/contents/files/article/' + name,
    {
      method: 'GET',
      headers: {
        Authorization: 'token ' + leftToken + rightToken,
      },
    }
  )
    .then((res) => res.json())
    .then((res) => {
      return res
    })
}
async function sendNewArticle(title, content, catagory) {
  const time = Date.now()
  return await fetch(
    'https://api.github.com/repos/xiaozhaoqi/privateRepository/contents/files/article/' +
    title +
    '-' +
    catagory +
    '-' +
    time +
    '.md',
    {
      method: 'PUT',
      headers: {
        Authorization: 'token ' + leftToken + rightToken,
      },
      body: JSON.stringify({
        message: 'AutoPush Article: ' + title,
        // @ts-ignore
        content: btoa(
          unescape(
            encodeURIComponent(
              JSON.stringify({
                title: title,
                content: content,
                time: time,
                catagory: catagory,
              })
            )
          )
        ),
      }),
    }
  )
    .then((res) => res.json())
    .then((res) => {
      return true
    })
    .catch((err) => {
      return false
    })
}
async function updateArticle(sha, oldTitle, newTitle, oldCatagory, newCatagory, createTime, content) {
  // ç§æœ‰åº“ä»…ä»¥æ–‡ä»¶åä½œä¸ºæ ‡è¯†ï¼Œä½¿ç”¨å…ˆåˆ é™¤å†æ–°å¢çš„æ–¹å¼ï¼Œå¯ä»¥æ»¡è¶³å¯¹æ–‡ä»¶åè¿›è¡Œå˜æ›´çš„éœ€è¦ã€‚
  const removeRes = await removeArticle({ sha, title: oldTitle, catagory: oldCatagory, time: createTime })
    .then((e) => { return e })
    .catch((e) => { return e })
  if (removeRes === 'remove failed') {
    return Promise.reject('update failed')
  }
  return await sendNewArticle(newTitle, content, newCatagory)
}
async function removeArticle(article) {
  const token = prompt('ä½ æ­£åœ¨å˜æ›´ç§æœ‰åº“çš„æ–‡ä»¶å­˜å‚¨ï¼Œè¾“å…¥Token:')
  if (token !== 'rm') {
    alert(token + ' ä¸æ˜¯æœ‰æ•ˆçš„ Token')
    return Promise.reject('remove failed');
  }
  return await fetch(
    'https://api.github.com/repos/xiaozhaoqi/privateRepository/contents/files/article/' +
    article.title +
    '-' +
    article.catagory +
    '-' +
    article.time +
    '.md',
    {
      method: 'DELETE',
      headers: {
        Authorization: 'token ' + leftToken + rightToken,
      },
      body: JSON.stringify({
        message: 'AutoDelete article: ' + article.title,
        // @ts-ignore
        sha: article.sha,
      }),
    }
  )
    .then((res) => res.json())
    .then((res) => {
      return res
    })
    .catch((err) => {
      console.log(err)
    })
}
